import React from 'react';
import { Link } from 'react-router';

export default class Hello extends React.Component {
  render() {
    return (
      <div>
          <Link to="/">
            <i className="fa fa-arrow-left fa-3x" />
          </Link>
          <h1>Hello Electron </h1></div>
    );
  }
}



